import { listMenu, getMenu, delMenu, addMenu, updateMenu } from "@/api/system/menu";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
import IconSelect from "@/components/IconSelect";
export default {
  name: "Menu",
  components: {
    Treeselect: Treeselect,
    IconSelect: IconSelect
  },
  data: function data() {
    return {
      // 
      loading: true,
      // 
      showSearch: true,
      // 
      menuList: [],
      // 
      menuOptions: [],
      // 
      title: "",
      // 
      open: false,
      // 
      visibleOptions: [],
      // 
      statusOptions: [],
      // 
      queryParams: {
        menuName: undefined,
        visible: undefined
      },
      // 
      form: {},
      // 
      rules: {
        menuName: [{
          required: true,
          message: "",
          trigger: "blur"
        }],
        orderNum: [{
          required: true,
          message: "",
          trigger: "blur"
        }],
        path: [{
          required: true,
          message: "",
          trigger: "blur"
        }]
      }
    };
  },
