import store from "@/store";
import { VueCropper } from "vue-cropper";
import { uploadAvatar } from "@/api/system/user";
export default {
  components: {
    VueCropper: VueCropper
  },
  props: {
    user: {
      type: Object
    }
  },
  data: function data() {
    return {
      // 
      open: false,
      // cropper
      visible: false,
      // 
      title: "",
      options: {
        img: store.getters.avatar,
        //
        autoCrop: true,
        // 
        autoCropWidth: 200,
        // 
        autoCropHeight: 200,
        // 
        fixedBox: true //  

      },
      previews: {}
    };
  },
  methods: {
    // 
    editCropper: function editCropper() {
      this.open = true;
    },
    // 
    modalOpened: function modalOpened() {
      this.visible = true;
    },
    // 
    requestUpload: function requestUpload() {},
    // 
    rotateLeft: function rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 
    rotateRight: function rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 
    changeScale: function changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 
    beforeUpload: function beforeUpload(file) {
      var _this = this;

      if (file.type.indexOf("image/") == -1) {
        this.msgError(",JPGPNG");
      } else {
        var reader = new FileReader();
        reader.readAsDataURL(file);

        reader.onload = function () {
          _this.options.img = reader.result;
        };
      }
    },
    // 
    uploadImg: function uploadImg() {
      var _this2 = this;

      this.$refs.cropper.getCropBlob(function (data) {
        var formData = new FormData();
        formData.append("avatarfile", data);
        uploadAvatar(formData).then(function (response) {
          _this2.open = false;
          _this2.options.img = process.env.VUE_APP_BASE_API + response.imgUrl;
          store.commit('SET_AVATAR', _this2.options.img);

          _this2.msgSuccess("");

          _this2.visible = false;
        });
      });
    },
    // 
    realTime: function realTime(data) {
      this.previews = data;
    }
  }
};