import { listTable, previewTable, delTable, genCode, synchDb } from "@/api/tool/gen";
import importTable from "./importTable";
import { downLoadZip } from "@/utils/zipdownload";
export default {
  name: "Gen",
  components: {
    importTable: importTable
  },
  data: function data() {
    return {
      // 
      loading: true,
      // 
      uniqueId: "",
      // 
      ids: [],
      // 
      tableNames: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      tableList: [],
      // 
      dateRange: "",
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        tableName: undefined,
        tableComment: undefined
      },
      // 
      preview: {
        open: false,
        title: "",
        data: {},
        activeName: "domain.java"
      }
    };
  },
  created: function created() {
    this.getList();
  },
  activated: function activated() {
    var time = this.$route.query.t;

    if (time != null && time != this.uniqueId) {
      this.uniqueId = time;
      this.resetQuery();
    }
  },
  methods: {
    /**  */
    getList: function getList() {
      var _this = this;

      this.loading = true;
      listTable(this.addDateRange(this.queryParams, this.dateRange)).then(function (response) {
        _this.tableList = response.rows;
        _this.total = response.total;
        _this.loading = false;
      });
    },

    /**  */
    handleQuery: function handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },

    /**  */
    handleGenTable: function handleGenTable(row) {
      var _this2 = this;

      var tableNames = row.tableName || this.tableNames;

      if (tableNames == "") {
        this.msgError("");
        return;
      }

      if (row.genType === "1") {
        genCode(row.tableName).then(function (response) {
          _this2.msgSuccess("" + row.genPath);
        });
      } else {
        downLoadZip("/tool/gen/batchGenCode?tables=" + tableNames, "ruoyi");
      }
    },

    /**  */
    handleSynchDb: function handleSynchDb(row) {
      var _this3 = this;

      var tableName = row.tableName;
      this.$confirm('"' + tableName + '"', "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(function () {
        return synchDb(tableName);
      }).then(function () {
        _this3.msgSuccess("");
      });
    },

    /**  */
    openImportTable: function openImportTable() {
      this.$refs.import.show();
    },

    /**  */
    resetQuery: function resetQuery() {
      this.dateRange = [];
      this.resetForm("queryForm");
      this.handleQuery();
    },

    /**  */
    handlePreview: function handlePreview(row) {
      var _this4 = this;

      previewTable(row.tableId).then(function (response) {
        _this4.preview.data = response.data;
        _this4.preview.open = true;
      });
    },
    // 
    handleSelectionChange: function handleSelectionChange(selection) {
      this.ids = selection.map(function (item) {
        return item.tableId;
      });
      this.tableNames = selection.map(function (item) {
        return item.tableName;
      });
      this.single = selection.length != 1;
      this.multiple = !selection.length;
    },

    /**  */
    handleEditTable: function handleEditTable(row) {
      var tableId = row.tableId || this.ids[0];
      this.$router.push("/gen/edit/" + tableId);
    },

    /**  */
    handleDelete: function handleDelete(row) {
      var _this5 = this;

      var tableIds = row.tableId || this.ids;
      this.$confirm('"' + tableIds + '"?', "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(function () {
        return delTable(tableIds);
      }).then(function () {
        _this5.getList();

        _this5.msgSuccess("");
      });
    }
  }
};