import { list, forceLogout } from "@/api/monitor/online";
export default {
  name: "Online",
  data: function data() {
    return {
      // 
      loading: true,
      // 
      total: 0,
      // 
      list: [],
      pageNum: 1,
      pageSize: 10,
      // 
      queryParams: {
        ipaddr: undefined,
        userName: undefined
      }
    };
  },
  created: function created() {
    this.getList();
  },
  methods: {
    /**  */
    getList: function getList() {
      var _this = this;

      this.loading = true;
      list(this.queryParams).then(function (response) {
        _this.list = response.rows;
        _this.total = response.total;
        _this.loading = false;
      });
    },

    /**  */
    handleQuery: function handleQuery() {
      this.pageNum = 1;
      this.getList();
    },

    /**  */
    resetQuery: function resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },

    /**  */
    handleForceLogout: function handleForceLogout(row) {
      var _this2 = this;

      this.$confirm('"' + row.userName + '"?', "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(function () {
        return forceLogout(row.tokenId);
      }).then(function () {
        _this2.getList();

        _this2.msgSuccess("");
      });
    }
  }
};