import request from '@/utils/request'; // 

export function listCarousel(query) {
  return request({
    url: '/system/carousel/list',
    method: 'get',
    params: query
  });
} // 

export function getCarousel(carouselId) {
  return request({
    url: '/system/carousel/' + carouselId,
    method: 'get'
  });
} // 

export function addCarousel(data) {
  return request({
    url: '/system/carousel',
    method: 'post',
    data: data
  });
} // 

export function updateCarousel(data) {
  return request({
    url: '/system/carousel',
    method: 'put',
    data: data
  });
} // 

export function delCarousel(carouselId) {
  return request({
    url: '/system/carousel/' + carouselId,
    method: 'delete'
  });
} // 

export function exportCarousel(query) {
  return request({
    url: '/system/carousel/export',
    method: 'get',
    params: query
  });
}